/* Code */

code,
kbd,
tt,
var,
samp,
pre {
  font-family: monospace, serif;
  -webkit-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
  font-style: normal;
}

pre {
  margin: 15px 0;
  padding: 15px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  overflow: auto;
}

/* Various */

address {
  margin: 0 0 15px;
  font-style: normal;
  line-height: inherit;
}

abbr,
abbr[title] {
  text-decoration: none;
  border: 0;
}

mark {
  color: inherit;
  font: inherit;
  background: none;
}

strong,
b {
