select {
  display: inline-block;
  font-size: inherit;
  width: 100%;
  max-width: 100%;
  height: 45px;
  padding: 10px 12px;
  box-shadow: none;
  line-height: normal;
  border: 2px solid #ebebeb;
  border-radius: 30px;
