/* Buttons
  =================================== */

button:focus {
  outline: 1px dotted #fac600;
}

.btn,
.comment-reply-link,
input[type="submit"],
input[type="reset"],
button[type="submit"] {
  display: inline-block;
  font-weight: normal;
  margin: 0;
  line-height: normal;
  border: 0;
  box-shadow: none;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  white-space: nowrap;
  -webkit-transition: 0.18s background-color ease, 0.18s color ease,
    0.18s border-color ease;
  transition: 0.18s background-color ease, 0.18s color ease,
    0.18s border-color ease;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border-radius: 0;
  width: auto;
  height: auto;
  background: none;
  color: #fff;
  background-color: #fac600;
  border: 0;
  text-transform: lowercase;
  font-weight: 500;
  letter-spacing: -0.025em;
  line-height: 1;
  font-family: inherit;
  padding: 16px 40px 15px;
  font-size: 16px;
  border-radius: 30px;
}

.btn.disabled,
.btn:disabled,
.comment-reply-link.disabled,
.comment-reply-link:disabled,
