}

/* -----------------------------------------
    02. Main Navigation
    ----------------------------------------- */

.nav {
  display: block;
}

.navigation-main {
  margin: 0;
  padding: 0;
  list-style: none;
  line-height: normal;
}

.navigation-main::after {
  content: "";
  display: table;
  clear: both;
}

.head-mast [class^="col-lg-12"] .navigation-main {
  margin-top: 10px;
}

.navigation-main li {
  position: relative;
}

.navigation-main > li {
  display: inline-block;
}

.navigation-main li a.active {
  font-weight: 500;
}

.navigation-main li a:hover {
  font-weight: 500;
}

.navigation-main a {
  display: block;
  white-space: nowrap;
}

.navigation-main ul {
  text-align: left;
  position: absolute;
  z-index: 10;
  visibility: hidden;
  opacity: 0;
  -webkit-transition: opacity 0.25s ease 0.2s, visibility 0s ease 0.35s,
    -webkit-transform 0.25s ease 0.2s;
  transition: opacity 0.25s ease 0.2s, visibility 0s ease 0.35s,
    -webkit-transform 0.25s ease 0.2s;
  transition: transform 0.25s ease 0.2s, opacity 0.25s ease 0.2s,
    visibility 0s ease 0.35s;
  transition: transform 0.25s ease 0.2s, opacity 0.25s ease 0.2s,
    visibility 0s ease 0.35s, -webkit-transform 0.25s ease 0.2s;
  -webkit-transform: translate(0, 10px);
  -ms-transform: translate(0, 10px);
  transform: translate(0, 10px);
  margin-left: 12px;
}

.navigation-main ul ul {
  top: -12px;
  left: 100%;
  margin: 0;
  -webkit-transform: translate(-10px, 0);
  -ms-transform: translate(-10px, 0);
  transform: translate(-10px, 0);
}

.navigation-main > li:first-child ul {
  margin-left: 0;
}

.navigation-main li:hover > ul {
  -webkit-transition-delay: 0s, 0s, 0s;
  transition-delay: 0s, 0s, 0s;
  visibility: visible;
  opacity: 1;
}

.navigation-main li:hover ul {
  z-index: 15;
}

.navigation-main > li:hover > ul {
  -webkit-transform: translate(0, 0);
  -ms-transform: translate(0, 0);
  transform: translate(0, 0);
}

.navigation-main li li:hover > ul {
  -webkit-transform: translate(0, 0);
  -ms-transform: translate(0, 0);
  transform: translate(0, 0);
}

.navigation-main > .nav-open-left ul {
  right: 0;
}

.navigation-main > .nav-open-left ul ul {
  right: 100%;
  left: auto;
}

.navigation-main li .nav-open-left ul {
  right: 100%;
  left: auto;
}

.navigation-main li .nav-open-left.menu-item-has-children > a::after {
  content: "\f0d9";
  font-size: 12px;
}

.navigation-main li.nav-open-left .menu-item-has-children > a::after {
  content: "\f0d9";
  font-size: 12px;
}

.navigation-main ul {
  padding: 15px 0;
  list-style: none;
  background-color: #fff;
  min-width: 200px;
  border-radius: 5px;
  box-shadow: 0 0 24px rgba(0, 0, 0, 0.08);
}

.navigation-main a {
  position: relative;
  color: #464646;
  font-size: 14px;
}

.navigation-main > li > a {
  padding: 10px 0;
  margin: 0 20px;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: -0.025em;
}

.navigation-main > li:first-child > a {
  margin-left: 0;
}

.navigation-main li li a {
  padding: 6px 20px;
}

.navigation-main li li:hover > a,
.navigation-main li li > a:focus,
.navigation-main li .current-menu-item > a,
.navigation-main li .current-menu-parent > a,
.navigation-main li .current-menu-ancestor > a {
  color: #fac600;
}

.navigation-main .menu-item-has-children > a::after {
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  content: "\f107";
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  font-size: 10px;
  position: relative;
  top: -4px;
  right: -4px;
  line-height: normal;
}

.navigation-main li .menu-item-has-children > a {
  padding-right: 25px;
}

.navigation-main li .menu-item-has-children > a::after {
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  content: "\f105";
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  position: absolute;
  right: 10px;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.navigation-main .nav-button > a {
  border: none;
  background-color: #fac600;
  font-size: 12px;
  border-radius: 20px;
  padding: 7px 23px;
  margin-left: 10px;
  font-weight: 800;
}

.navigation-main .nav-button > a::before {
  display: none;
}

.navigation-main .nav-button:last-child > a {
  margin-right: 0;
}

.navigation-main .mega-menu > ul {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}

.navigation-main .mega-menu > ul > li > a {
  font-weight: 700;
  font-size: 1.05em;
}

.navigation-main .mega-menu > ul > li > a,
.navigation-main .mega-menu > ul > li > a:focus,
.navigation-main .mega-menu > ul > li.current-menu-item > a,
.navigation-main .mega-menu > ul > li.current-menu-parent > a,
.navigation-main .mega-menu > ul > li.current-menu-ancestor > a {
  background-color: transparent;
  color: initial;
}

.navigation-main .mega-menu > ul ul {
  display: block;
  opacity: 1;
  box-shadow: none;
  padding: 0;
  margin: 0;
  position: static;
  background: none;
  min-width: 180px;
  -webkit-transform: translate(0, 10px);
  -ms-transform: translate(0, 10px);
  transform: translate(0, 10px);
}

.navigation-main .mega-menu > ul a::after {
  display: none;
}

.navigation-main .mega-menu:hover ul ul {
  opacity: 1;
  visibility: visible;
  -webkit-transform: none;
  -ms-transform: none;
  transform: none;
  -webkit-transition-delay: 0s;
  transition-delay: 0s;
