/* Modal
  =================================== */

.modal {
  display: none;
  position: fixed;
  z-index: 99999;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.6);
  text-align: center;
  font-size: 14px;
  font-weight: 700;
  -webkit-user-select: none;
  user-select: none;
}

.modal h3 {
  font-size: 14px;
  font-weight: 400;
}
.modal label {
  text-align: center;
  float: left;
  padding-left: 20px;
  padding-top: 10px;
}

.modal-content {
  padding: 30px;
  max-width: 500px;
  width: 100% !important;
  background-color: #f7f7f7;
  margin: 5% auto 0% auto; /* 5% from the top, 15% from the bottom and centered */
  border-radius: 2px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  overflow: hidden;
  font-family: roboto;
  border-radius: 15px;
}

.modal input[type="text"],
input[type="password"] {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  box-sizing: border-box;
}

.modal .chosen-select {
  text-align: left;
}

.modal .filter-form-group {
  margin: 14px 0;
}

.modal .chosen-select option:disabled {
  text-align: left;
  font-weight: 400;
}

.modal input[type="submit"] {
  color: white;
  padding: 14px 20px;
  margin: 15px 0;
  border: none;
  cursor: pointer;
  width: 100%;
  text-transform: uppercase;
}

.close {
  color: #aaa;
  float: right;
  text-align: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.login-help {
  padding-top: 20px;
  font-size: 12px;
}

@-webkit-keyframes slideIn {
  from {
    bottom: -300px;
    opacity: 0;
  }
  to {
    bottom: 0;
    opacity: 1;
  }
}

@keyframes slideIn {
  from {
    bottom: -300px;
