/* Table
  =================================== */

table {
  margin: 25px auto;
  width: 100%;
  border-collapse: separate;
  border-bottom: 2px solid #fac600;
  box-shadow: 0 7px 10px 2px rgba(0, 0, 0, 0.04);
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  border-radius: 20px;
  border-spacing: 0;
}
table tr {
  text-align: center;
}

table tr:hover {
  background: #f4f4f4;
}
table tr:hover td {
  color: #555;
}
table tr.row-first {
  height: 60px;
}
table th,
table td {
  color: #999;
  border: 1px solid #eee;
  padding: 12px 35px;
  border-collapse: collapse;
}
table th {
  background: #fac600;
  color: #fff;
  text-transform: uppercase;
  font-size: 12px;
}
table td.last {
  border-right: none;
  border-left: none;
  border-bottom: none;
}
table th,
table tr:not(:last-child) td {
  border-bottom: none;
  border-right: none;
  border-left: none;
}
table th:not(:last-child),
table td:not(:last-child) {
  border-left: none;
  border-bottom: none;
  border-right: none;
}

@media (max-width: 767px) {
  /* Responsive Table */
  .table-responsive {
    display: block;
    position: relative;
    width: 100%;
  }

  .table-responsive thead,
  .table-responsive tbody,
  .table-responsive th,
  .table-responsive td,
  .table-responsive tr {
    display: block;
  }
  .table-responsive td,
  .table-responsive th {
    min-height: 50px;
    height: auto;
    width: auto;
    padding: 15px 10px 10px 10px;
  }

  .table-responsive thead {
    float: left;
  }

  .table-responsive tbody {
    width: auto;
    position: relative;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    white-space: nowrap;
  }

  .table-responsive tbody tr {
    display: inline-block;
  }

  .table td:last-child {
    border-right: #999999 solid 1px;
  }
}

